<template>
  <div class="dashboard-tabs elevation-1">
    <v-tabs
      class="d-none d-md-flex"
      v-model="tabModel"
      active-class="tab-item-active"
      :height="65"
      :slider-size="0"
    >
       <v-tab
        class="text-none tab-item"
        :to="{ name: 'admin-revenues' }"
      >
        Faturado
      </v-tab>
      <v-tab
        class="text-none tab-item"
        :to="{ name: 'admin-schedules' }"
      >
        Agendamentos
      </v-tab>
      <v-tab
        class="text-none tab-item"
        :to="{ name: 'admin-scheduling-requests' }"
      >
        <v-badge
          :color="tabModel === '/admin/dashboard/solicitacoes' ? 'success' : 'primary'"
          :content="qtdRequestsPending"
          :value="qtdRequestsPending"
        >
          Solicitaes
        </v-badge>
      </v-tab>
    </v-tabs>
    <div v-if="!noChooseDate" class="tabs-period">
      <date-picker-range
        v-if="this.period === 'period'"
        :clearable="true"
        :outlined="true"
        :dense="true"
        :close="true"
        :value="dateRangeValue"
        cssClass="date-rage"
        @change="changePeriodRange"
        @close="close"
      />
      <select-field
        v-else
        :value="period"
        class="period" 
        :items="periods"
        :hideDetails="true"
        :outlined="true"
        :dense="true"
        @onChange="changePeriod"
      />
    </div>
  </div>
</template>

<script>
import DatePickerRange from '@/components/globais/form/DatePickerRange'
import SelectField from '@/components/globais/form/select-field/SelectField'
import DateMixin from '@/mixins/DateMixin'
import DashboardMixin from '@/mixins/DashboardMixin'
import ScheduleMixin from '@/mixins/ScheduleMixin'
export default {
  name: 'DashboardTabs',
  components: { DatePickerRange, SelectField },
  mixins: [ DateMixin, DashboardMixin, ScheduleMixin ],
  data: () => ({
    tabModel: 0,
    dateRangeValue: [],
    noChooseDate: false,
    period: null,
    periods: [
      { text: 'Hoje', value: 'today' },
      { text: 'Semana', value: 'week' },
      { text: 'Ms', value: 'month' },
      { text: 'Ano', value: 'year' },
      { text: 'Mximo', value: null },
      { text: 'Perodo', value: 'period' }
    ],
    periodRange: null
  }),
  computed: {
    qtdRequestsPending () {
      return this.qtdRequestsPendingState > 0 ? this.qtdRequestsPendingState : null
    }
  },
  watch: {
    tabModel () {
      this.noChooseDate = this.$route.meta.noChooseDate
    }
  },
  mounted () {
    this.tabModel = this.$route.meta.tab
    this.noChooseDate = this.$route.meta.noChooseDate
    const date = new Date()
    let endDate = this.formatDateToDateEn(date)
    let startDate = this.formatDateToDateEn(date)
    if (this.periodState) {
      this.period = this.periodState.type || null
      startDate = this.periodState.startDate || startDate
      endDate = this.periodState.endDate || endDate
    }
    this.dateRangeValue = [
      startDate,
      endDate
    ]
    this.periodRange = this.getPeriod(this.period)
  },
  methods: {
    changePeriod (val) {
      this.period = val
      this.periodRange = this.getPeriod(val)
      const period = {
        type: this.period,
        startDate: this.periodRange.startDate,
        endDate: this.periodRange.endDate
      }
      this.setPeriodState(period)
    },
    changePeriodRange (value) {
      if (this.period === 'period') {
        const startDate = value.length > 0 ? value[0] : null
        const endDate = value.length > 1 ? value[1] : null
        this.periodRange = {
          startDate,
          endDate
        }
      }
    },
    close () {
      this.changePeriod(null)
    },
    getPeriod (period) {
      let today = new Date()
      let startDate = this.formatDateToDateEn(today)
      let endDate = null
      const firstDayOfMonth = new Date(today.getFullYear(), today.getMonth(), 1)
      const lastDayOfMonth = new Date(today.getFullYear(), today.getMonth() + 1, 0)
      const firstDayOfYear = new Date(new Date().getFullYear(), 0, 1)
      const lastDayOfYear = new Date(new Date().getFullYear(), 11, 31)
      switch (period) {        
        case 'week':
          var days = 6 - today.getDay()
          today.setDate(today.getDate() + days)
          endDate = this.formatDateToDateEn(today)
          break;
        
        case 'month':
          startDate = this.formatDateToDateEn(firstDayOfMonth)
          endDate = this.formatDateToDateEn(lastDayOfMonth)
          break;

        case 'year':
          startDate = this.formatDateToDateEn(firstDayOfYear)
          endDate = this.formatDateToDateEn(lastDayOfYear)
        break;

        case null:
          startDate = null
          endDate = null
        break;
      
        default:
          endDate = this.formatDateToDateEn(today)
          break;
      }
      return {
        startDate,
        endDate
      }
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@/scss/theme.scss';
.dashboard-tabs {
  border-radius: 15px;
  overflow: hidden;
  display: flex;
  align-items: center;
  background: #fff;
}
.tab-item { letter-spacing: 0; font-size: 1rem; padding: 0 40px; }
.tab-item-active { background: #000; color: $success !important; }
.tabs-period { padding: 0 30px; }
.period { border-radius: 45px; min-width: 250px; }
@media all and (max-width: 959px) {
  .dashboard-tabs { box-shadow: none !important; background: transparent; }
  .tabs-period { padding: 0; width: 250px; background: #fff; border-radius: 45px; }
}
</style>

<style lang="scss">
.dashboard-tabs {
  .date-rage { border-radius: 45px !important; width: 250px; }
}
</style>