<template>
  <div class="dashboard container">
    <template v-if="barber.barbershop_id">
      <template v-if="barbershop">
        <dashboard-tabs
          class="mt-8 mt-md-4"
        />
        <router-view class="mt-4 mt-md-8"></router-view>
      </template>
    </template>
    <template v-else>
      <without-barber />
    </template>
    <help-fab />
    <overlay ref="overlay" />
  </div>
</template>

<script>
import DashboardTabs from '@/components/admin/dashboard/DashboardTabs'
import HelpFab from '@/components/admin/HelpFab';
import Overlay from '@/components/globais/overlay/Overlay'
import WithoutBarber from '@/components/admin/barber/WithoutBarber'
import BarberMixin from '@/mixins/BarberMixin'
import BarbershopMixin from '@/mixins/BarbershopMixin' 
import PermissionMixin from '@/mixins/PermissionMixin'

export default {
  name: 'Dashboard',
  components: { DashboardTabs, HelpFab, Overlay, WithoutBarber },
  mixins: [ BarberMixin, BarbershopMixin, PermissionMixin ],
  data: () =>  ({
    tabModel: 0
  }),
  watch: {
    barbershop (val) {
      if (val ) this.$refs.overlay.hideOverlay()
    }
  },
  mounted () {
    if (this.barber.barbershop_id && !this.barbershop) {
      this.$refs.overlay.showOverlay()
    }
  }
}
</script>

<style lang="scss" scoped>
.dashboard { min-height: calc(100vh - 96px); }
</style>