<template>
  <v-menu
    v-model="menu2"
    :close-on-content-click="false"
    :nudge-right="40"
    transition="scale-transition"
    offset-y
    min-width="200px"
  >
    <template v-slot:activator="{ on, attrs }">
      <v-text-field
        v-model="dateModel"
        :label="label"
        locale="pt"
        :prepend-inner-icon="icon"
        readonly
        :outlined="outlined"
        :dense="dense"
        v-bind="attrs"
        :success="success"
        :hide-details="hideDetails"
        :class="cssClass"
        :append-icon="clearable ? 'close' : ''"
        @click:append="close"
        v-on="on"
      >
        <template v-slot:prepend-inner>
          <v-icon
            v-if="icon"
            class="material-icons-outlined"
            :class="success ? 'success--text' : ''"
            dense
          >
            event
          </v-icon>
        </template>
      </v-text-field>
    </template>
    <v-date-picker
      v-model="dates"
      :min="min"
      :max="max"
      range
      color="primary"
    >
      <v-spacer></v-spacer>
      <v-btn
        text
        color="primary"
        class="font-weight-bold"
        @click="menu2 = false"
      >
        OK
      </v-btn>
    </v-date-picker>
  </v-menu>
</template>

<script>
import DateMixin from '@/mixins/DateMixin'
export default {
  name: 'DatePickerRange',
  mixins: [ DateMixin ],
  props: {
    clearable: { default: false },
    id: { default: null },
    label: { default: null },
    icon: { default: null },
    success: { default: false },
    min: { default: null },
    max: { default: null },
    value: { default: null  },
    outlined: { default: null },
    dense: { default: false },
    hideDetails: { default: true },
    cssClass: { default: null }
  },
  data: () => ({
    dates: [],
    menu2: false
  }),
  mounted () {
    if (this.value) this.dates = this.value
    else {
      //let date = new Date()
      //this.date = this.formatDateToDateEn(date)
      this.dates = []
    }
  },
  computed: {
    dateModel: {
      set () {},
      get () {
        const min = this.dates[0]
        const max = this.dates[1]
        let text = ''
        if (min) {
          let [year, month, day] = min.split('-')
          text += `${day}/${month}/${year}`
        }
        if (max) {
          let [year, month, day] = max.split('-')
          text += ` ~ ${day}/${month}/${year}`
        }
        //let date = new Date(this.date)
        //date.setDate(date.getDate() + 1)
        //return this.formatDateToDatePtBr(date)
        return text
      }
    }
  },
  watch: {
    value (val) {
      this.dates = val
    },
    dates (dates) {
      this.$emit('change', dates, this.id)
    }
  },
  methods: {
    close () {
      this.dates = [],
      this.$emit('close', this.id)
    }
  }
}
</script>